var app= require("../../common/common.js"); 
var per_page=0;
var isfirst=true;
var catid=0;
var gid=0;
export default{

	data:function(){
		return {
			pageLoad:false, 
			pageHide:false,
			pageData:{},
		}
		
	},
	onLoad:function(option){
		gid=option.gid;
		catid=option.catid;
		uni.setNavigationBarTitle({
			title: '我的帖子'
		});
		this.getPage();
	},
	 
	onShow:function(){
		if(this.pageHide){
			this.pageHide=false;
			this.getPage();
		}			
	},
	onHide:function(){
		this.pageHide=true;
	}, 
	onReachBottom:function(){
		this.getList();
	},
	onPullDownRefresh:function(){
		this.refresh();
	},
	methods:{
		getPage:function(){
			var that=this;
			uni.request({
				url:app.apiHost+"/module.php?fromapp=wxapp&m=forum&a=my&ajax=1",
				data:{
					gid:gid,
					catid:catid,
					authcode:app.getAuthCode()
				},
				success:function(res){
					//登录
					if(res.data.error==1000){
						uni.navigateTo({
							url:"/pages/login/index",
						})
					}else{
						isfirst=false;
						that.pageLoad=true;
						that.pageData=res.data.data;
						per_page=res.data.data.per_page;
					}
					 
				}
			})
		},
		setCat:function(cid){
			catid=cid;
			isfirst=true;
			per_page=0;
			if(catid==0){
				this.defaultActive=activeClass;
			}else{
				this.defaultActive="";
			}
			var catlist=this.pageData.catlist;
			for(var i in catlist){
				if(catlist[i].catid==catid){
					catlist[i].isactive=1;
				}else{
					catlist[i].isactive=0;
				}
			}
			this.pageData.catlist=catlist;
			this.getList();
		 },
		getList:function(){
			var that=this;
			if(!isfirst && per_page==0) return false;
			uni.request({
				url:app.apiHost+"/module.php?fromapp=wxapp&m=forum&a=my&ajax=1",data:{
					per_page:per_page,
					catid:catid,
					gid:gid,
					authcode:app.getAuthCode()
				},
				success:function(res){
					
					if(!res.data.error){
						if(isfirst){
							that.pageData.list=res.data.data.list;
							isfirst=false;
						}else{
							
							that.pageData.list=app.json_add(that.pageData.list,res.data.data.list);
						}
						per_page=res.data.data.per_page;  
						
					}
					
					
				}
			})
		},
		goForum: function (id) {
			uni.navigateTo({
				url: "../forum/show?id=" + id
			})
		},
		refresh:function(){
			this.getPage();
			setTimeout(function(){
				uni.stopPullDownRefresh();
			},1000)
		},
		loadMore:function(){
			this.getList();
		},
		del:function(id){
			var that=this;
			uni.showModal({
				title: '提示',
				content: '删除后不可恢复，确认删除？',
				success: function (res) {
					if (res.confirm) {
						uni.request({
							url:app.apiHost+"/module.php?fromapp=wxapp&m=forum&a=delete&ajax=1&id="+id,
							data:{
								authcode:app.getAuthCode()
							},
							success:function(res){
								uni.showToast({
									title:res.data.message
								});
								if(!res.data.error){
									that.getPage();
								}
							}
						})
					} else if (res.cancel) {
						
					}
				}
			});
		}
	},
}
